<script>
import counter from 'vue3-autocounter';
export default {
	components: {
		counter,
	},
	data: () => {
		return {
			experienceTitle: 'AÃ±os de experiencia',
			githubTitle: 'Horas de trabajo',
			feedbackTitle: 'Feedback positivo',
			projectsTitle: 'Proyectos completos',
			proyectos: 13,
			exp: 5,
			horas: 2,
			positivo: 91
		};
	},
};
</script>

<template>
	<div class="mt-10 sm:mt-20 dark:bg-primary-light bg-ternary-dark shadow-sm">
		<!-- About me counters -->
		<div
			class="font-general-regular container mx-auto py-20 block sm:flex sm:justify-between sm:items-center"
		>
			<!-- Years of experience counter -->
			<div class="mb-20 sm:mb-0">
				<counter
					ref="counter"
					:startAmount="0"
					:endAmount="exp"
					:duration="1"
					:autoinit="true"
					@finished="alert(`Counting finished!`)"
					class="font-general-medium text-4xl font-bold dark:text-secondary-dark text-secondary-light mb-2"
					aria-label="About Status Counter"
				/>
				<span
					class="block text-md dark:text-ternary-dark text-ternary-light"
				>
					{{ experienceTitle }}
				</span>
			</div>

			<!-- GitHub stars counter -->
			<div class="mb-20 sm:mb-0">
				<counter
					ref="counter"
					:startAmount="0"
					:endAmount="horas"
					:duration="1"
					suffix="k+"
					:autoinit="true"
					@finished="alert(`Counting finished!`)"
					class="font-general-medium text-4xl font-bold dark:text-secondary-dark text-secondary-light mb-2"
				/>
				<span
					class="block text-md dark:text-ternary-dark text-ternary-light"
					>{{ githubTitle }}</span
				>
			</div>

			<!-- Positive feedback counter -->
			<div class="mb-20 sm:mb-0">
				<counter
					ref="counter"
					:startAmount="0"
					:endAmount="positivo"
					:duration="1"
					suffix="%"
					:autoinit="true"
					@finished="alert(`Counting finished!`)"
					class="font-general-medium text-4xl font-bold dark:text-secondary-dark text-secondary-light mb-2"
				/>
				<span
					class="block text-md dark:text-ternary-dark text-ternary-light"
				>
					{{ feedbackTitle }}
				</span>
			</div>

			<!-- Projects completed counter -->
			<div class="mb-20 sm:mb-0">
				<counter
					ref="counter"
					:startAmount="0"
					:endAmount="proyectos"
					:duration="1"
					:autoinit="true"
					@finished="alert(`Counting finished!`)"
					class="font-general-medium text-4xl font-bold dark:text-secondary-dark text-secondary-light mb-2"
				/>
				<span
					class="block text-md dark:text-ternary-dark text-ternary-light"
				>
					{{ projectsTitle }}
				</span>
			</div>
		</div>
	</div>
</template>
