import { ref } from "vue";
import { API_PRODUCCION } from "../utils/constants";
import axios from "axios";

const useAbout = () => {
  const about = ref();
  const imagenUrl = ref();
  const loading = ref(false)

  const verAbout = async () => {
    try {
      about.value = null;
      imagenUrl.value = null;
      loading.value = true;

      const { data } = await axios.get(
        `${API_PRODUCCION}/api/about?populate=imagen`
      );

      about.value = data.data.attributes.descipcion;
      imagenUrl.value = data.data.attributes.imagen.data.attributes.url;
    } catch (error) {
      console.log(error);
    }
    loading.value = false;

  };

  verAbout();

  return {
    about,
    imagenUrl,
    loading
  };
};

export default useAbout;
